import React, { Component } from 'react'
import { commit } from '../../constants/color';
import { getMyRanking } from '../../api/APIRank'
import { getMyCommitMap } from '../../api/APIGithub'
import styles from './UserInfo.module.css'
import subStyle from './MyCommitInfo.module.css'
import CommitBox from '../dashboard/CommitBox'


class MyCommitInfo extends Component {
    constructor(props) {
        super(props)
    }

    componentDidMount() {
        let score = () => {
            getMyRanking()
                .then(res => {
                    console.log(res)
                    if(res.status== -200 ){
                        this.props.props.saveScore(res.response)
                    }
                }).catch(error => {
                    this.setState({
                        loading: false
                    });
                    console.log(error);
                });
        }

        let commitMap = () => {
            getMyCommitMap()
                .then(res => {
                    console.log(res)
                    if(res.status== -200 ){
                        this.props.props.saveCommitMap(res.response)
                    }
                }).catch(error => {
                    this.setState({
                        commitMapLoading: false
                    });
                    console.log(error);
                });
        }

        if (this.props.props.commitMap == null) {
            commitMap();
        }
        if (this.props.props.score == null) {
            score();
        }
    }


    render() {
        const { commitMap, score, user } = this.props.props

        return (
            <div className={subStyle.container}>
                <div className={subStyle.columnBox}>
                    <div className={styles.subtitle} style={{marginBottom: '1rem'}}>EWHA RANKING | Github</div>
                    <div className= {subStyle.scoreBox}>
                        <span className={[subStyle.scoreItem, subStyle.greyText].join(" ")}></span>
                        <span className={[subStyle.titleItem, subStyle.greyText].join(" ")}></span>
                        <span className={[subStyle.scoreItem, subStyle.greyText].join(" ")}> </span>
                        <span className={[subStyle.scoreItem, subStyle.greyText].join(" ")}> </span>
                        <span className={[subStyle.scoreItem, subStyle.greyText].join(" ")}> </span>
                    </div>
                    <div className={[subStyle.scoreBox, subStyle.score].join(" ")}>
                    <span className={subStyle.scoreItem}></span>
                        <span className={[subStyle.titleItem, subStyle.greenText].join(" ")}>{user && user.name}</span>
                        <span className={subStyle.scoreItem}>{score && score.quarterRank.commitCount}</span>
                        <span className={subStyle.scoreItem}>{score && score.quarterRank.commitMaxCombo}</span>
                        <span className={[subStyle.scoreItem, subStyle.greenText].join(" ")}>{score && score.quarterRank.score}</span>
                    </div>
                    <div className={[subStyle.scoreBox, subStyle.score].join(" ")}>
                    <span className={subStyle.scoreItem}></span>
                        <span className={[subStyle.titleItem, subStyle.greenText].join(" ")}>{user && user.name}</span>
                        <span className={subStyle.scoreItem}>{score && score.weekRank.commitCount}</span>
                        <span className={subStyle.scoreItem}>{score && score.weekRank.commitMaxCombo}</span>
                        <span className={[subStyle.scoreItem, subStyle.greenText].join(" ")}>{score && score.weekRank.score}</span>
                    </div>
                </div>
                <div className={subStyle.columnBoxMin}>
                    <div className={styles.subtitle} style={{marginBottom: 0}}></div>
                    <CommitBox commitMap={commitMap}/>
                </div>
            </div>
        )

    }
}
export default MyCommitInfo